@import url('https://fonts.googleapis.com/css?family=Raleway|Work+Sans:200');

.PortfolioItems {
    background-color: rgb(138, 79, 187);
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 50px;
    line-height: 500%;
    overflow: hidden;
}

.PortfolioLink {
    display: inline-block;
    text-align: center;
    margin: 0 25px;
    text-decoration: none;
}

.PortfolioImage {
    width: 250px;
    height: 250px;
    margin: 0 auto;
    background-position: center;
    background-size: cover;
    filter: brightness(.6);
    border-radius: 15px;
    border: peachpuff 5px solid;
}

.PortfolioImage:hover {
    filter: brightness(1)
}

.PortfolioLink h4 {
    color: peachpuff;
    text-align: center;
    font-size: 1.5rem;
    font-family: 'Raleway', sans;
    margin: 0;
    
}

.PortfolioLink p {
    color: peachpuff;
    text-align: center;
    font-size: 1.2rem;
    font-family: 'Raleway', sans;
    margin: 0;
    height: 15px;
}

.VCS {
    background-image: url(../assets/vcs.png);
}

.MK {
    background-image: url(../assets/mk.png);
}

.DB {
    background-image: url(../assets/db.png);
