@import url(https://fonts.googleapis.com/css?family=Raleway|Work+Sans:200);
@import url(https://fonts.googleapis.com/css?family=Raleway|Work+Sans:200,300);
@import url(https://fonts.googleapis.com/css?family=Raleway|Work+Sans:200);
@import url(https://fonts.googleapis.com/css?family=Raleway|Work+Sans:200);
@import url(https://fonts.googleapis.com/css?family=Raleway|Work+Sans:300);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Jumbotron {
    background-image: url(/static/media/fuji.2c38bc8c.jpg);
    background-size: cover;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.JumbotronContent {
    padding-bottom: 21vh;
}

.JumbotronContent h1 {
    font-family: 'Raleway', sans-serif;
    color: rgb(133, 41, 219);
    font-size: 6rem;
    margin: 0;
    padding-bottom: 20px;
    text-align: center;
}

.JumbotronContent h4 {
    font-family: 'Work Sans', sans-serif;
    color: rgb(133, 41, 219);
    font-size: 4rem;
    font-weight: 200;
    margin: 0;
    text-align: center;
}

.InfoContainer {
    background-color: peachpuff;
    width: 100vw;
    margin: 0;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding-bottom: 50px;
}

.TechImage img {
    max-height: 200px;
}

.Bio {
    padding: 5vh 15vw 20px 15vw;
    margin: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Bio {
    font-family: 'Work Sans', serif;
    font-size: 1.5rem;
    font-weight: 300;
    color: rgb(48, 0, 94);
    text-align: center;
}

.Tech {
    padding: 0 10vw 50px 10vw;
    text-align: center;
    margin: 0px;
    margin-top: 50px;
    font-family: 'Work Sans', serif;
    font-size: 1.5rem;
    font-weight: 300;
    color: rgb(48, 0, 94);
    width: 100%;
}


.TechLogo {
    text-align: center;
    width: 100px;
    display: inline-block;
    padding: 0 25px 0 25px;
}

.TechLogo img {
    margin: 0 auto;
    max-height: 100px;
    max-width: 100px;
}

.TechLogo h2 {
    font-family: 'Work Sans', serif;
    font-size: 1.5rem;
    font-weight: 300;
    color: rgb(48, 0, 94);
    text-align: center;
}

.TechLogo p {
    color: rgb(48, 0, 94);
    font-size: 1.5rem;
    margin-top: 5px;
}
.PortfolioContainer {
    font-family: 'Raleway', serif;
    background-color: rgb(138, 79, 187);
}

.PortfolioContainer h1 {
    font-family: 'Raleway', serif;
    font-size: 4rem;
    color: peachpuff;
    text-align: center;
    margin: 0 auto;
    padding: 50px 0;
}
.PortfolioItems {
    background-color: rgb(138, 79, 187);
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 50px;
    line-height: 500%;
    overflow: hidden;
}

.PortfolioLink {
    display: inline-block;
    text-align: center;
    margin: 0 25px;
    text-decoration: none;
}

.PortfolioImage {
    width: 250px;
    height: 250px;
    margin: 0 auto;
    background-position: center;
    background-size: cover;
    -webkit-filter: brightness(.6);
            filter: brightness(.6);
    border-radius: 15px;
    border: peachpuff 5px solid;
}

.PortfolioImage:hover {
    -webkit-filter: brightness(1);
            filter: brightness(1)
}

.PortfolioLink h4 {
    color: peachpuff;
    text-align: center;
    font-size: 1.5rem;
    font-family: 'Raleway', sans;
    margin: 0;
    
}

.PortfolioLink p {
    color: peachpuff;
    text-align: center;
    font-size: 1.2rem;
    font-family: 'Raleway', sans;
    margin: 0;
    height: 15px;
}

.VCS {
    background-image: url(/static/media/vcs.2536cf0d.png);
}

.MK {
    background-image: url(/static/media/mk.de5b8646.png);
}

.DB {
    background-image: url(/static/media/db.bc33d071.png);
}

.FV {
    background-image: url(/static/media/fv.178588f6.png);
}

.SCR {
    background-image: url(/static/media/scr.a864a38a.png);
}



.Contact {
    background-color: peachpuff;
    padding: 50px 0;
}

.Contact h4 {
    width: 75vw;
    text-align: center;
    margin: 0 auto;
    font-family: 'Work Sans', sans-serif;
    font-weight: 300;
    font-size: 1.5rem;
    color: rgb(48, 0, 94)
}

.Contact h4 a {
    color: rgb(0, 189, 72)
}
html, body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  background-color: rgb(138, 79, 187);
}


